html, body {
    margin: 0;
    padding: 0;
}
.songs-container {
    margin: 40px 18px;
}

.songs-container>div {
    margin-bottom: 10px;
}
.song-item {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 5px 8px;
    background: #131313;
}

.song-item img {
    display: block;
    width: 2rem;
}

.song-item a {
    display: block;
    color: #efe;
    text-decoration: none;
}

.play-link:hover {
    text-decoration: underline;
}

.song-info {
    display: flex;
    flex-direction: column;
}

.song-info>span {
    display: block;
    margin: 3px;
}

.artist {
    font-size: 0.85em;
    color: #bcb;
}
body {
    background: #020202;
    font-size: 18px;
    font-family: 'Courier New', monospace;
}
